/* Reset & Base Styles */
body,
html {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f4f4f4;
}

.App {
    width: 100%;
}

/* Navbar Styling */
nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2c3e50;
    padding: 1rem 2rem;
    color: white;
    flex-wrap: wrap;
}

nav ul {
    display: flex;
    gap: 2rem;
    list-style: none;
    padding: 0;
    margin: 0;
}

nav li a {
    color: white;
    text-decoration: none;
    font-weight: bold;
}

nav li a:hover {
    color: #f1c40f;
}

/* Parent Section */
.parent {
    padding: 2rem;
}

/* Task Card Styling */
.task {
    background-color: #ffffff;
    border-radius: 10px;
    padding: 2rem;
    max-width: 200px;
    margin: 2rem auto;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.task:hover {
    background-color: #ecf0f1;
}

.task h1 {
    margin-top: 0;
    color: #2c3e50;
}

.task p {
    font-size: 1rem;
    color: #555;
}

/* Modal Overlay */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
}

/* Modal Content Box */
.modal-content {
    background-color: white;
    padding: 2rem;
    border-radius: 10px;
    width: 90%;
    max-width: 500px;
    position: relative;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.modal-content h2 {
    margin-top: 0;
    color: #2c3e50;
}

.modal-content p {
    color: #333;
    line-height: 1.5;
    margin-bottom: 1rem;
}

.modal-content a {
    display: inline-block;
    margin-top: 1rem;
    color: #2980b9;
    font-weight: bold;
    text-decoration: none;
}

.modal-content a:hover {
    text-decoration: underline;
}

/* Modal Close Button */
.close-button {
    position: absolute;
    top: 10px;
    right: 15px;
    font-size: 1.5rem;
    cursor: pointer;
    color: #999;
    transition: color 0.2s ease;
}

.close-button:hover {
    color: #e74c3c;
}


input {
    width: 300px;
    /* or any desired width */
    padding: 8px;
    margin-right: 10px;
    border-radius: 5px;
    border: 1px solid #0f0e0e;
}

button {
    padding: 8px 12px;
    border: none;
    border-radius: 5px;
    background-color: #2b7cff;
    color: white;
    cursor: pointer;
}

button:hover {
    background-color: #1a5ecc;
}

/* Responsive Layout */
@media (max-width: 600px) {
    nav {
        flex-direction: column;
        text-align: center;
        gap: 1rem;
    }

    nav ul {
        flex-direction: column;
        gap: 1rem;
    }

    .task {
        padding: 1.5rem;
        margin: 1rem;
    }

    .modal-content {
        padding: 1.5rem;
    }
}